import styled from "styled-components";
import cross from "../../assets/faq/cross.png";
import "./Fix.css";

export const Question = styled.summary`
    font-size: 22px;
    color: #fff;
    font-weight: 600;
    padding-bottom: 16px;
    display: block;
    width: calc(100% - 25px);
    position: relative;
    cursor: pointer;
    line-height: 1.2;

    &::after{
        content: url(${cross});
        position: absolute;
        right: -5px;
        width: 20px;
        bottom: calc((100% - 20px) / 2);

        @media screen and (max-width: 900px){
            width: 10px;
            zoom: 60%;
            top: 10px;
        }
    }

`;

//#5C5E62
export const QuestionContainer = styled.details`
    padding-bottom: 1rem;
    margin: 4rem 0;
    border-bottom: 4px solid #5C5E62;
    transition: height 1s ease;
    line-height: 1.3;

    @media screen and (max-width: 900px){
        padding-bottom: 0;
    }
`;

export const Answer = styled.p`
    font-size: 22px;
    color: #5C5E62;
    padding-bottom: 1rem;
    font-weight: 500;
`;

export const FaqContainer = styled.div`
    width: 100%;
    padding: 30px 10%;
    margin-bottom: 4rem;

    @media screen and (max-width: 900px){
        margin-bottom: 0;
    }
`;