import styled from "styled-components";
import bg from "../../assets/Plans/t.png";

export const Bundle = styled.div`
    background: transparent;
`;

export const BundleHeader = styled.h6`
    color: #56585C;
    font-weight: bold;
    font-size: 20px;
`;

export const BundleTitle = styled.h4`
    color: #fff;
    padding: 20px 0;
    font-weight: 500;
    font-size: 32px;
`;

export const BundleElement = styled.li`
    display: inline-block;
    width: 33%;
    color: #fff;
    font-weight: 500;
    font-size: 20px;
    padding: 12px 0;
    padding-right: 24px;

    &::before{
        content: '●';
        color: ${(props) => props.charColor};
        margin-right: 8px;
        margin-bottom: 2px;

        @media screen and (max-width: 900px){
            font-size: 10px;
            margin-bottom: 16px;
        }
    }

    @media screen and (max-width: 900px){
        width: 33%;
        padding: 6px 0;
        padding-right: 8px;
    }
`;

//should be changed from h5 to a 
export const BundleBtn = styled.h5`
    color: ${(props) => props.charColor};
    font-weight: bold;
    font-size: 21px;
    padding-top: 32px;
    position: relative;
    
    &::after{
        position: absolute;
        content: '➜';
        color: ${(props) => props.charColor};
        padding-left: 22px;
        font-size: 22px;
        bottom: -3px;
    }
`;

export const Provider = styled.div`
    position: relative;
    padding: 74px 0 54px;
    color: #363636;
    -webkit-text-decoration: none;
    text-decoration: none;
    border-radius: 12px;
    width: 100%;
    text-align: center;

    @media screen and (max-width: 700px) {
        width: 90%;
        margin-bottom: 24px;
    }
    @media screen and (min-width: 700px) {
        max-width: 300px;
        margin-right: 40px;
        margin-bottom: 40px;
    }
    background: transparent;
    background-image: url(${bg});
    background-size: cover;
`;

export const ProviderIcon = styled.img`
    height: auto;
    max-width: 100%;
    display: block;
    margin: auto;
    background: transparent !important;
`;

export const ProviderName = styled.div`
    color: #fff;
    padding: 32px;
    font-weight: 500;
    font-size: 32px;
    background: transparent;
`;