import React from 'react'
import { Heading, Title } from '../../shared/Generics'
import { ArtistName, ArtistsContainer, Card, CardImage, Cards, CardText, Hyperlink } from './Artists.elements'
import dummyImg from "../../assets/artists/singing.jpg"
//todo clean imports of artists related stuff

var lis = [];

for (var i=0; i<12; i++) {
    lis.push(
        <Card>
            <CardImage src={dummyImg} />
            <ArtistName>Artist Name</ArtistName>
            <CardText>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Vestibulum lectus turpis, iaculis vitae fringilla scelerisque, tempus in risus. Aliquam egestas ligula eget felis accumsan scelerisque. Aliquam.</CardText>
            <Hyperlink>Hyperlink</Hyperlink>
        </Card>
    );
}


export const Artists = () => {
    return (
        <React.Fragment>
            <Heading> artists we're connected with </Heading>
            <Title> We're privileged to work with some of the top artists in the industry. </Title>

            <ArtistsContainer>
                <Cards>                    
                    {lis}
                </Cards>
            </ArtistsContainer>
        </React.Fragment>
    )
}

