import styled from "styled-components";

export const FooterWrapper = styled.footer`
    background: #232428;
    padding: 30px 10%;
`;

export const Foot = styled.div`
    display: flex;
    width: 100%;
    padding-top: 25px;
    border-top: 4px solid #313236;

    @media screen and (max-width: 800px){
        display: block;
    }
`;

export const Copyright = styled.div`
    flex-grow: 1;
    color: #fff;
    font-size: 23px;
    font-weight: bold;

    &::before{
        content: '© ';
        font-size: 30px;
    }

    @media screen and (max-width: 800px){
        text-align: center;
    }
`;

export const FooterIcons = styled.ul`
    list-style: none;
    display: flex;
    place-content: center;

    li {
        padding: 0px 10px;
    }

    @media screen and (max-width: 800px){
        margin-top: 15px;
    }
    
`;