import React, { useState } from 'react'
import { Head, Logo, Nav, NavLinks, Link, NavIcons, Hamburger, MobileMenu } from './Header.elements'
import logo from "../../assets/landing page/logo.png";
import {FaLinkedinIn as LinkedinIcon, FaPinterestP as PinterestIcon, FaYoutube as YoutubeIcon} from "react-icons/fa";
import {ImSoundcloud as SoundcloudIcon} from "react-icons/im";
import { HiOutlineMenu } from "react-icons/hi";

export const Header = () => {

    const [toggle, setToggle]= React.useState(false)
    const style = { color: "white", fontSize: "36px" };

    const showMenu = () => {
        setToggle(!toggle)
      }
    return (
        <div>
            <Head>
                <Logo>
                    <img src={logo} />
                </Logo>
                <Nav>
                    <NavLinks>
                        <li><Link to="/home"> Home </Link></li>
                        <li><Link to="/bundles"> Bundles </Link></li>
                        <li><Link to="/artists"> Artists </Link></li>
                        <li><Link to="/faq"> FAQ </Link></li>
                    </NavLinks>
                </Nav>
                <Nav>
                    <NavIcons>
                        <li><SoundcloudIcon style={style} /></li>
                        <li><LinkedinIcon style={style} /></li>
                        <li><PinterestIcon style={style} /></li>
                        <li><YoutubeIcon style={style} /></li>
                    </NavIcons>
                </Nav>
                <Hamburger onClick={showMenu}>
                    <HiOutlineMenu onClick={showMenu} style={{color: "white", fontSize: "40px", marginTop: "12px"}}/>
                </Hamburger>
            </Head>
            <MobileMenu open={toggle}>
                <li><Link to="/home" onClick={showMenu}> Home </Link></li>
                <li><Link to="/bundles" onClick={showMenu}> Bundles </Link></li>
                <li><Link to="/artists" onClick={showMenu}> Artists </Link></li>
                <li><Link to="/faq" onClick={showMenu}> FAQ </Link></li>
            </MobileMenu>
        </div>
    )
}

    