import styled from "styled-components";

export const ArtistsContainer = styled.div`
    width: 100%;
    padding: 30px 10%;
    padding-right: -40px;
`;

export const Cards = styled.div`
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-top: 15px;
    padding: 1.5%;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
`;

export const Card = styled.div`
    position: relative;
    margin-bottom: 20px;
    padding-bottom: 30px;
    color: #363636;
    text-decoration: none;
    border-radius: 4px;
    width: 100%;

    @media screen and (max-width: 700px) {
          width: 100%;
      }
      @media screen and (min-width: 700px) {
          max-width: 300px;
          margin-right: 40px;
          margin-bottom: 40px;
      }
`;

export const CardImage = styled.img`
    width: 100%;
    border-radius: 15px;
    height: auto;
`;

export const ArtistName = styled.h5`
    font-size: 20px;
    color: #fff;
    font-weight: 600;
    padding: 1.5rem 0 0rem;
`;

export const CardText = styled.h6`
    font-size: 18px;
    color: #505256;
    font-weight: 400;
    padding: 1rem 0 0rem;
    line-height: 1.3;
`;

export const Hyperlink = styled.a`
    color: #A9A7AC; 
    font-size: 18px;
    font-weight: 600;
    padding: 1.3rem 0 0rem;
    display: block;
`;