import { NavLink } from "react-router-dom";
import styled from "styled-components";

export const Head = styled.header`
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 60px 10%;
    background-color: #232428;

    @media screen and (max-width: 1200px){
        padding: 32px 10% 16px;
    }
`;

export const Logo = styled.div`
    cursor: pointer;

    @media screen and (max-width: 1200px){
        img{
            max-width: 90%;
        }
    }
`;

export const Nav = styled.div``;

export const NavLinks = styled.ul`
    list-style: none;
    display: flex;

    @media screen and (max-width: 1200px){
        display: none;
    }

    li {
        padding: 0px 15px;
    }
    
`;

export const NavIcons = styled(NavLinks)`
    li {
        padding: 0px 10px;
    }
`;

export const Link = styled(NavLink)`
    font-weight: bold;
    color: #535559;
    text-decoration: none;
    transition: color 0.3s ease 0s;
    font-size: 20px;
    padding-bottom: 6px;

    &.active{
        color: #fff;
        border-bottom: 2px solid #E756BE;
    }

`;

export const Hamburger = styled.div`
    display: none;

    @media screen and (max-width: 1200px){
        display: block;
    }

`;

export const MobileMenu = styled.div`
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 9999999;
    overflow: hidden;
    background: #232428;
    display: ${(prop) => prop.open ? 'block' : 'none'};
    padding: 30px 10%;

    li {
        list-style-type: none;
        text-align: right;
        margin: 16px 0;
    }

    ${Link} {
        font-size: 32px;
    }
`;
