import React from 'react'
import { Form } from '../../shared/form/Form'
import Slider from "react-slick";
import { Author, Bundle, BundleNumber, BundleText, BundleTitle, Company, Info, InputContainer, LeftArrow, Logo, PurpleInput, Quote, ReadMore, RightArrow, Slide, SliderContainer, StatisticCard, StatisticHeader, StatisticLogo, StatisticText, StatisticTitle } from './Home.elements';
import logo from "../../assets/landing page/logo.png";
import { Heading, Title, ArtistName, ArtistsContainer, Card, CardImage, Cards, CardText, Hyperlink, FlexContainer, Row, Right, Left, ContainerHeader, ContainerTitle, ContainerText } from '../../shared/Generics';
import dummyImg from "../../assets/artists/singing.jpg"
import purple from "../../assets/landing page/megaphone puple.png";
import blue from "../../assets/landing page/megaphone blue.png";
import green from "../../assets/landing page/megaphone green.png";
import pink from "../../assets/landing page/megaphone pink.png";
import phone from "../../assets/landing page/Phone.png";
import mac from "../../assets/landing page/Mac.png";

var lis = [];
var statistics = [];

for (var i=0; i<4; i++) {
    lis.push(
        <Card>
            <CardImage src={dummyImg} />
            <ArtistName>Artist Name</ArtistName>
            <CardText>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Vestibulum lectus turpis, iaculis vitae fringilla scelerisque, tempus in risus. Aliquam egestas ligula eget felis accumsan scelerisque. Aliquam.</CardText>
            <Hyperlink>Hyperlink</Hyperlink>
        </Card>
    );
}

const colors = [blue, purple, pink, green];

for (var i=0; i<4; i++){
    statistics.push(
        <StatisticCard>
            <StatisticLogo src={colors[i]}/>
            <StatisticTitle>Statistic {i + 1}</StatisticTitle>
            <StatisticHeader>Lorem ipsum dolor.</StatisticHeader>
            <StatisticText>
                Lorem ipsum dolor sit amet, consectetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore.
            </StatisticText>
        </StatisticCard>
    );
}

export const Home = () => {
    
    function SampleNextArrow(props) {
        const { className, style, onClick } = props;
        return (
        <RightArrow
            onClick={onClick}
        />
        );
    }
    
    function SamplePrevArrow(props) {
        const { className, style, onClick } = props;
        return (
        <LeftArrow
            onClick={onClick}
        />
        );
    }
    
    const settings = {
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        nextArrow: <SampleNextArrow />,
        prevArrow: <SamplePrevArrow />
      };

    return (
        <React.Fragment>
            
            <FlexContainer>
                <Row>
                    <Right>
                        <img src={phone} />
                    </Right>
                    <Left>
                        <ContainerHeader>Lorem ipsum dolor sit amet, consetetur.</ContainerHeader>
                        <ContainerTitle>Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy elitr.</ContainerTitle>
                        <ContainerText>Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et doloe magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores.</ContainerText>
                        <InputContainer>
                            <PurpleInput placeholder={"Lorem ipsum"} />
                        </InputContainer>
                    </Left>
                </Row>
            </FlexContainer>

            <Cards>                    
                {statistics}
            </Cards>

            <FlexContainer>
                <Row>
                    <Left>
                        <Bundle>
                            <BundleNumber style={{backgroundColor: "#3971FF"}}>01</BundleNumber>
                            <BundleTitle>Bundle One</BundleTitle>
                            <BundleText>Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam.</BundleText>
                            <ReadMore>Read more</ReadMore>
                        </Bundle> 
                        <Bundle>
                            <BundleNumber style={{backgroundColor: "#9557D6"}}>02</BundleNumber>
                            <BundleTitle>Bundle Two</BundleTitle>
                            <BundleText>Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam.</BundleText>
                            <ReadMore>Read more</ReadMore>
                        </Bundle>
                        <Bundle>
                            <BundleNumber style={{backgroundColor: "#EF476F"}}>03</BundleNumber>
                            <BundleTitle>Bundle Three</BundleTitle>
                            <BundleText>Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam.</BundleText>
                            <ReadMore>Read more</ReadMore>
                        </Bundle>   
                    </Left>
                    <Right>
                        <img src={mac} />
                    </Right>
                </Row>
            </FlexContainer>

            <Heading> artists we're connected with </Heading>
            <Title> We're privileged to work with some of the top artists in the industry. </Title>
            <ArtistsContainer>
                <Cards>                    
                    {lis}
                </Cards>
            </ArtistsContainer>
            <SliderContainer>
                <Slider {...settings}>
                    <Slide>
                        <Logo src={logo} />
                        <Quote> Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nam vel fermentum tortor, eget pellentesque nibh. Etiam rutrum aliquam lacus, ut laoreet risus ornare non. Praesent sollicitudin mollis turpis eget commodo. Sed tincidunt, felis nec hendrerit placerat. </Quote>
                        <Info> 
                            <Author> Jonathon Doe </Author>
                            <Company> Doe Technologies Limited </Company>    
                        </Info>
                    </Slide>

                    <Slide>
                        <Logo src={logo} />
                        <Quote> Test </Quote>
                        <Info> 
                            <Author> Author </Author>
                            <Company> Company </Company>    
                        </Info>
                    </Slide>
                </Slider>
            </SliderContainer>
            <Form />
        </React.Fragment>
    )
}

