import styled from "styled-components";

export const Heading = styled.h6`
    font-size: 16px;
    color: #5C5E62;
    font-weight: bolder;
    text-transform: uppercase;
    text-align: center;
    padding: 3rem 1rem 1rem;
    max-width: 600px;
    margin: auto;
`;

export const Title = styled.h1`
    font-size: 28px;
    color: #fff;
    font-weight: 600;
    padding-bottom: 32px;
    text-align: center;
    padding: 1rem 1rem 3rem;
    max-width: 600px;
    margin: auto;

    @media screen and (max-width: 900px){
        padding-bottom: 0;
    }
`;

export const ArtistsContainer = styled.div`
    width: 100%;
    padding: 30px 10%;
    padding-right: -40px;
`;

export const Cards = styled.div`
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-top: 15px;
    padding: 1.5%;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
`;

export const Card = styled.div`
    position: relative;
    margin-bottom: 20px;
    padding-bottom: 30px;
    color: #363636;
    text-decoration: none;
    border-radius: 4px;
    width: 100%;

    @media screen and (max-width: 700px) {
          width: 100%;
      }
      @media screen and (min-width: 700px) {
          max-width: 300px;
          margin-right: 40px;
          margin-bottom: 40px;
      }
`;

export const CardImage = styled.img`
    width: 100%;
    border-radius: 15px;
    height: auto;
`;

export const ArtistName = styled.h5`
    font-size: 20px;
    color: #fff;
    font-weight: 600;
    padding: 1.5rem 0 0rem;
`;

export const CardText = styled.h6`
    font-size: 18px;
    color: #505256;
    font-weight: 400;
    padding: 1rem 0 0rem;
    line-height: 1.3;
`;

export const Hyperlink = styled.a`
    color: #A9A7AC; 
    font-size: 18px;
    font-weight: 600;
    padding: 1.3rem 0 0rem;
    display: block;
`;

export const FlexContainer = styled.div`
    margin: 0 auto;
    float: none;
    margin-bottom: 8rem;
    padding: 30px 10%;
    padding-top: 60px;
    max-width: 1981px;

    @media screen and (max-width: 900px){
        padding-top: 0;
        padding-bottom: 0;
    }
`;

export const Row = styled.div`
    display: flex;
    flex-wrap: wrap;
    margin: 24px 0;
    align-items: center;
    flex-direction: row-reverse;
`;

export const Right = styled.div`
    position: relative;
    width: 100%;
    padding: 24px;
    flex: 0 0 44%;
    max-width: 44%;

    img{
        height: auto;
        max-width: 100%;
        display: block;
    }

    @media screen and (max-width: 900px){
        flex: 0 0 100%;
        max-width: 100%;
    }
`;

export const Left = styled.div`
    position: relative;
    width: 100%;
    padding: 24px 48px 24px 24px;
    flex: 0 0 53%;
    max-width: 53%;

    img{
        height: auto;
        max-width: 100%;
        display: block;
    }

    @media screen and (max-width: 900px){
        flex: 0 0 100%;
        max-width: 100%;
        padding: 0;
    }
`;

export const ContainerHeader = styled.h3`
    color: #5C5E62;
    font-size: 20px;
    font-weight: bold;
    padding: 8px 0 16px;
    text-transform: uppercase;
`;

export const ContainerTitle = styled.h2`
    color: #FFFFFF;
    font-size: 32px;
    font-weight: bold;
    padding: 8px 0 16px;
    line-height: 1.4;
`;

export const ContainerText = styled.h3`
    color: #5C5E62;
    font-size: 22px;
    font-weight: 500;
    line-height: 1.6;
    padding: 8px 0 16px;
`;
