import styled from "styled-components";

export const FormWrapper = styled.div`
    width: 100%;
    padding: 30px 10%;
    display: flex;

    @media screen and (max-width: 1000px){
        flex-direction: column;
    }
`;

export const FormNotes = styled.div`
    width: calc(100% - 47px);
    padding-right: 47px;

    @media screen and (max-width: 1000px){
        width: 100%;
        padding: 0;
        padding-bottom: 47px;
    }
`;

export const FormTitle = styled.h1`
    font-size: 28px;
    color: #fff;
    font-weight: 600;
    padding-bottom: 32px;
`;

export const FormText = styled.p`
    font-size: 22px;
    color: #5C5E62;
    font-weight: 300;
`;

export const FormModal = styled.form`
    width: 100%;
`;

export const FormLabel = styled.label`
    font-size: 24px;
    font-weight: bold;
    color: #fff;
`;

export const FormInput = styled.input`
    border:none;
    background-image:none;
    background-color:transparent;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
    outline: none;

    border-bottom: 4px solid #E756BE;
    width: 100%;
    color: #5C5E62;
    font-size: 24px;
    font-weight: 300;
    margin: 10px 0px 60px 0px;
    padding: 10px 0px;
`;

export const FormTextArea = styled.textarea`
    border:none;
    background-image:none;
    background-color:transparent;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
    outline: none;
    resize: none;

    border-bottom: 4px solid #E756BE;
    width: 100%;
    color: #5C5E62;
    font-size: 24px;
    font-weight: 300;
    margin: 10px 0px 30px 0px;
    padding: 10px 0px;

    height: 110px;

    &::-webkit-scrollbar {
        display: none;
    }

    @media screen and (max-width: 900px){
        height: 160px;
    }
`;

export const SendButton = styled.button`
    float: right;
    border:none;
    background-image:none;
    background-color:transparent;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
    outline: none;

    font-size: 24px;
    font-weight: bold;
    color: #fff;
`;