import React from 'react'
import { Copyright, Foot, FooterIcons, FooterWrapper } from './Footer.elements'
import {FaLinkedinIn as LinkedinIcon, FaPinterestP as PinterestIcon, FaYoutube as YoutubeIcon} from "react-icons/fa";
import {ImSoundcloud as SoundcloudIcon} from "react-icons/im";

export const Footer = () => {

    const style = { color: "#E756BE", fontSize: "36px" };

    return (
        <FooterWrapper>
            <Foot>
                <Copyright>2021 Vice Music Limited</Copyright>
                <FooterIcons>
                    <li><SoundcloudIcon style={style} /></li>
                    <li><LinkedinIcon style={style} /></li>
                    <li><PinterestIcon style={style} /></li>
                    <li><YoutubeIcon style={style} /></li>
                </FooterIcons>
            </Foot>
        </FooterWrapper>
    )
}

