import styled from "styled-components";
import sel from "../../assets/landing page/Quotemarks-left.png";
import leftArrow from "../../assets/landing page/arrow left.png";
import rightArrow from "../../assets/landing page/arrow right.png";

export const SliderContainer = styled.div`
    padding: 10rem 0 12rem 0;
    max-width: 1200px;
    margin: auto;

    @media screen and (max-width: 900px){
        padding-bottom: 5rem;
    }
`;

export const Slide = styled.div`
    display: block !important;
    position: relative;
    text-align: center;
    padding: 16px;
`;

export const Logo = styled.img`
    display: block;
    margin: auto;
`;

export const Quote = styled.div`
    color: #FFFFFF;
    font-size: 32px;
    font-weight: 500;
    line-height: 1.35;
    padding: 48px 32px 32px;
    position: relative;
    border-bottom: 4px solid #313236;

    &::before{
        display: block;
        content: url(${sel});
        top: 38px;
        left: 0;
        position: absolute;
        zoom: 75%;
    }

    &::after{
        display: block;
        content: url(${sel});
        bottom: 22px;
        right: 0;
        position: absolute;
        transform: scaleX(-1);
        zoom: 75%;
    }

    @media screen and (max-width: 900px){
        font-size: 26px;
    }
`;

export const Info = styled.div`
    max-width: calc(100% - 26%);
    margin: auto;
`;

export const Author = styled.h4`
    color: #FFFFFF;
    font-size: 22px;
    font-weight: 600;
    padding: 32px 0 16px;
`;

export const Company = styled.h5`
    color: #5C5E62;
    font-size: 20px;
    font-weight: 600;
    padding: 0px 0 16px;
`;

export const LeftArrow = styled.img`
    display: block;
    cursor: pointer;
    content: url(${leftArrow});
    position: absolute;
    bottom: 32px;
    left: 12%;
    z-index: 999;

    @media screen and (max-width: 900px){
        display: none;
    }
`;

export const RightArrow = styled.img`
    display: block;
    cursor: pointer;
    content: url(${rightArrow});
    position: absolute;
    bottom: 32px;
    right: 12%;
    z-index: 999;

    @media screen and (max-width: 900px){
        display: none;
    }
`;

export const StatisticCard = styled.div`
    position: relative;
    margin-bottom: 20px;
    padding-bottom: 30px;
    color: #363636;
    text-decoration: none;
    border-radius: 4px;
    width: 100%;

    @media screen and (max-width: 900px) {
        width: 80%;
    }
    @media screen and (min-width: 900px) {
        max-width: 300px;
        margin-right: 40px;
        margin-bottom: 40px;
    }
`;

export const StatisticLogo = styled.img`
    width: auto;
    height: auto;
`;

export const StatisticTitle = styled.h4`
    font-size: 32px;
    color: #B8B6BB;
    font-weight: bold;
    padding: 22px 0;
    border-bottom: 4px solid #313236;
`;

export const StatisticHeader = styled.h5`
    font-size: 20px;
    color: #B8B6BB;
    font-weight: bold;
    padding: 16px 0;
    text-transform: uppercase;
`;

export const StatisticText = styled.p`
    color: #5C5E62;
    font-size: 18px;
    font-weight: 500;
    line-height: 1.6;
`;

export const PurpleInput = styled.input`
    outline: none;
    width: 100%;
    color: #B8B6BB;
    font-size: 22px;
    font-weight: 500;
    padding: 16px 56px;
    border: 4px solid #E756BE;
    border-radius: 48px;
    position: relative;

    @media screen and (max-width: 900px){
        padding: 16px;
    }
`;

export const InputContainer = styled.form`
margin-top: 48px;
position: relative;

@media screen and (max-width: 900px){
    margin-top: 24px;
}

&::after{
    content: '';
    display: block;
    position: absolute;
    width: 50px;
    height: 50px;
    background: #e756be;
    z-index: 99999;
    right: 8px;
    bottom: calc(((100% - 48px) / 2) - 1px);
    border-radius: 50%;
}
`;

export const BundleNumber = styled.h6`
    padding: 16px 30px;
    background-color: #3971FF;
    font-size: 22px;
    font-weight: bold;
    color: #FFFFFF;
    max-width: min-content;
    border-radius: 48px;
    margin-bottom: 8px;
`;

export const BundleTitle = styled.h3`
    font-size: 22px;
    font-weight: bold;
    color: #FFFFFF;
    padding: 8px 0;
`;

export const BundleText = styled.p`
    color: #5C5E62;
    font-size: 20px;
    font-weight: 500;
    line-height: 1.6;
    padding: 8px 0;
`;

export const ReadMore = styled.h6`
    color: #B8B6BB;
    font-size: 20px;
    font-weight: bold;
    padding: 8px 0;
`;

export const Bundle = styled.div`
    padding: 0px 0px 48px;
`;